body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  background-color: rgb(4, 150, 255);
}

.aboutText *{
  background-color: rgb(245, 193, 24);
}

.aboutText {
  margin-top: 10px;
  display: flex;
}

.resumeText {
  margin-top: 10px;
  display: flex;
}

.resumeText * {
  padding: 5px;
  float: left;
  width: 100%;
}

.resumeText a {
  display: flex-end;
}

header h2 {
  float: left;
  margin-left: 20px;
}

.about p {
  padding-top: 10px;
  font-size: 16px;
}

.about {
  padding: 15px;
}

a {
  text-decoration: none;
  color: black;
}

.header {
  justify-content: space-between;
  width: 100%;
  position: fixed;
  color: rgb(247, 125, 25);
  background-color: rgb(0, 107, 166);
}

.header a {
  color: rgb(247, 125, 25);
}

.projects {
  margin: 20px;
  padding: 15px;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
  align-items: center;
}

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  max-width: 400px;
  float: left;
  margin: 20px;
  background-color: rgb(255, 188, 66);
}

.projectList a{
    background-color: rgb(255, 188, 66);
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

/* Add some padding inside the card container */
.container *{
  padding: 2px 4px;
  background-color: rgb(255, 188, 66);
}

.container h4 {
  padding: 2px 4px;
  font-size: 18px;
  background-color: rgb(255, 188, 66);
}

.container a {
  padding: 2px 4px;
  font-size: 18px;
  background-color: rgb(255, 188, 66);
}

.px-1 a {
    padding-top: 10px;
    padding-left: 20px;
}

.header .mx-2, .mx-1, header a, span {
  float: right;
  padding: 5px 8px;
  background-color: rgb(0, 107, 166);
  font-weight: bold;
  cursor: pointer;
  font-size: 24px;
  padding-left: 5px;
}

::marker {
    color: rgb(0, 107, 166);
}

.contactForm * {
    margin: 2px;
    padding: 2px;
    justify-content: space-evenly;
    float: left;
}

.contactForm h1 {
    width: 100%;
    margin: 2px;
    padding: 10px;
}

.contactForm div {
    width: 100%;
}

.contactForm input {
    width: 500px;
    background-color: white;
    border: solid 2px black;
}

.contactForm textarea {
    width: 500px;
    background-color: white;
    border: solid 2px black;
}

.contactForm button {
    color: white;
    background-color: black;
    border-top: 2px solid darkgrey;
    border-left: 2px solid darkgrey;

}

.contactForm label {
    font-weight: bold;
}

footer {
  text-align: center;
  width: 100%;
  background-color: rgb(0, 107, 166);
  padding: 10px;
  position: fixed;
  bottom: 0;
}

footer a {
    padding-left: 10px;
    padding-right: 10px;
    font-size: 36px;
    background-color: rgb(0, 107, 166);
}

footer i {
  background-color: rgb(0, 107, 166);
}

.card a {
    float: right;
}
